@import '../../stylesheets/variables';
.detail__container{
    width: 100%;
    max-width: 600px;
    margin: 80px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.experience__bars{
    // max-width: 400px;
    
}
.experience__title{
    margin-bottom: 5px;
    font-family: "Barlow", sans-serif;
    font-weight: 300;
    line-height: 1.6em;
    letter-spacing: 2px;
    color: $colorPink;
    @media all and (min-width:768px){
        color: $colorLight;
    }
}
.experience__bar{
    margin-top: 5px;
    margin-bottom: 15px;
    width: 100%;
    height: 10px;
    background-color: $colorLight;
    border-radius: 8px;
}
.fill__bar {
    background-color:$colorPurple;
    height: 10px;
    width: 50%;
    border-radius: 4px;
    box-shadow: 0px 0px 5px 0px rgba(255,255,255,1);
    @media all and (min-width:768px){
        background-color:$colorDarkBlue;
    }
}
.javascript{
    width: 100%;
}
.react {
    width: 90%;
}
.libraries {
    width: 100%
}
.css {
    width: 60%;
}
.html {
    width: 60%;
}
.otherlanguages{
    width: 100%;
}
